import React, { memo } from 'react';
import { compose, withState, lifecycle } from 'recompose';
import { withStyles, useTheme } from '@material-ui/core/styles';
import {
  useMediaQuery,
  Dialog,
  DialogContent,
  DialogActions,
  DialogTitle,
  IconButton,
  Grid,
  Button,
  FormControl,
  FormHelperText,
  TextField
} from '@material-ui/core';
import PropTypes from 'prop-types';
import CloseIcon from '@material-ui/icons/Close';
import { get as _get, mapValues as _mapValues, isEmpty as _isEmpty } from 'lodash';
import TicketsCategoriesService from '../../../services/ticket-categories';
import Tracker from '../../../utils/tracker';
import styles from './styles';

const NO_ASSIGNEE_LABEL = 'Nenhum responsvel';
const TicketCategoryFormModal = memo(({
  classes, onClose, formData, setFormData, ticketCategory, setErrors,
  loading, errors, assignees, setLoading, setAlert, refetch
}) => {
  const disableNextButton = (loading || _isEmpty(formData.title));
  const theme = useTheme();
  const fullScreen = useMediaQuery(theme.breakpoints.down('sm'));
  const onCancelPressed = () => {
    onClose();
  };
  const updateFormData = (object) => {
    setFormData({ ...formData, ...object });
  };
  const onUpdateTicketCategory = async () => {
    setLoading(true);
    try {
      const body = { id: ticketCategory.id, ...formData };
      await TicketsCategoriesService.updateCategory(body);
      Tracker.hit('TicketCategoryUpdated - Success');
      setAlert({ alertMsg: 'Categoria atualizada com sucesso', alertType: 'success', showAlert: true });
      onClose();
      refetch(true);
    } catch (err) {
      setErrors(_mapValues(_get(err, 'errors', {}), (e) => e[0]));
      setAlert({ alertMsg: 'Erro ao atualizar categoria', alertType: 'error', showAlert: true });
    }
    setLoading(false);
  };
  return (
    <Dialog
      fullScreen={fullScreen}
      keepMounted
      fullWidth
      maxWidth="sm"
      classes={{ paper: classes.dialogMemberForm }}
      open
    >
      <DialogTitle>
        Editar categoria
        <IconButton
          className={classes.closeButton}
          onClick={onCancelPressed}
          disabled={loading}
        >
          <CloseIcon />
        </IconButton>
      </DialogTitle>
      <DialogContent className={classes.content}>
        <Grid container direction="row" spacing={1}>
          <React.Fragment>
            <Grid item xs={12}>
              <FormControl fullWidth margin="normal" error={!!errors.title}>
                <TextField
                  disabled={loading}
                  label="Ttulo"
                  error={!!errors.title}
                  value={_get(formData, 'title', '')}
                  InputLabelProps={{ shrink: true }}
                  onChange={(e) => updateFormData({ title: e.target.value })}
                  variant="outlined"
                />
                {errors.title && <FormHelperText>{errors.title}</FormHelperText>}
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth margin="normal" error={!!errors.assigneeId}>
                <TextField
                  disabled={loading}
                  error={!!errors.assigneeId}
                  label="Responsvel"
                  select
                  value={_get(formData, 'assigneeId', '')}
                  SelectProps={{ native: true }}
                  InputLabelProps={{ shrink: true }}
                  onChange={(e) => updateFormData({ assigneeId: e.target.value })}
                  variant="outlined"
                >
                  <option value="">{NO_ASSIGNEE_LABEL}</option>
                  {assignees && assignees.map(a => <option key={a.id} value={a.id}>{`${a.name} ${a.lastname}`}</option>)}
                </TextField>
                {errors.assigneeId && <FormHelperText>{errors.assigneeId}</FormHelperText>}
              </FormControl>
            </Grid>
            <Grid item xs={12} sm={6}>
              <FormControl fullWidth margin="normal" error={!!errors.deadlineInDays}>
                <TextField
                  type="tel"
                  disabled={loading}
                  label="Prazo de resoluo (em dias)"
                  error={!!errors.deadlineInDays}
                  value={_get(formData, 'deadlineInDays', '')}
                  InputLabelProps={{ shrink: true }}
                  onChange={(e) => updateFormData({ deadlineInDays: (e.target.value || '').replace(/[\D]+/g, '') })}
                  variant="outlined"
                />
                {errors.deadlineInDays &&
                <FormHelperText>{errors.deadlineInDays}</FormHelperText>}
              </FormControl>
            </Grid>
          </React.Fragment>
        </Grid>
      </DialogContent>
      <DialogActions className={classes.actions}>
        <Grid container justify="space-between" spacing={2}>
          <Grid item xs={12} sm={6} />
          <Grid item xs={6} sm={3}>
            <Button
              fullWidth
              color="default"
              size="large"
              variant="contained"
              onClick={onCancelPressed}
              disabled={loading}
            >
              CANCELAR
            </Button>
          </Grid>
          <Grid item xs={6} sm={3}>
            <Button
              fullWidth
              color="primary"
              size="large"
              variant="contained"
              onClick={onUpdateTicketCategory}
              disabled={disableNextButton}
            >
              SALVAR
            </Button>
          </Grid>
        </Grid>
      </DialogActions>
    </Dialog>
  );
});

TicketCategoryFormModal.propTypes = {
  classes: PropTypes.object,
  onClose: PropTypes.func,
  loading: PropTypes.bool,
  errors: PropTypes.object,
  setErrors: PropTypes.func,
  formData: PropTypes.object,
  setFormData: PropTypes.func,
  ticketCategory: PropTypes.object,
  assignees: PropTypes.array,
  setLoading: PropTypes.func,
  setAlert: PropTypes.func,
  refetch: PropTypes.func
};

export default compose(
  withStyles(styles, { withTheme: true }),
  withState('formData', 'setFormData', {}),
  withState('loading', 'setLoading', false),
  withState('errors', 'setErrors', {}),
  lifecycle({
    componentDidMount() {
      const { setFormData, ticketCategory } = this.props;
      setFormData({
        title: ticketCategory.title,
        assigneeId: ticketCategory.assignee_id,
        deadlineInDays: ticketCategory.deadline_in_days
      });
    }
  })
)(TicketCategoryFormModal);
